import {Badge, Button} from "react-bootstrap";
import {state, switchVisibleChangePasswordModal, switchVisibleSubscribeManagement} from "../store";
import {observer} from "mobx-react-lite";

export const Profile = observer(() => {
    return <>
        <div>
            <h6>:</h6>
        </div>
        <div className="d-block d-lg-flex justify-content-around">
            <div className="flex-fill">
                <div>
                    <span>:</span> {state.state?.login}
                </div>

                <div>
                    <span> : </span>{new Date(state.state?.registerDate).toLocaleDateString()}
                </div>
            </div>
            <div className="flex-fill">
                <div>
                    <span>: </span>

                    {new Date(state.state?.endAccessDate) > new Date() &&
                        <Badge bg="success"> ( {new Date(state.state?.endAccessDate).toLocaleDateString()})</Badge>
                    }

                    {new Date(state.state?.endAccessDate) <= new Date() &&
                        <Badge bg="danger"> ( {new Date(state.state?.endAccessDate).toLocaleDateString()})</Badge>
                    }
                </div>
            </div>
        </div>
        <div className="d-flex mt-2">
            <Button variant="primary" size="sm" onClick={switchVisibleChangePasswordModal} className="me-2"> </Button>
            <Button variant="primary" size="sm" onClick={switchVisibleSubscribeManagement} className="me-2"> </Button>
        </div>
        <hr/>
    </>
})
